import axios from 'axios';
import CompanyBlockedException from '../exceptions/auth/CompanyBlockedException.ts';
import ApiService from './ApiService.ts';
import FreeTierExpired from '../exceptions/auth/FreeTierExpired.ts';

class AuthService {
  static async checkPhoneNumberJSON(phoneNumber: string) {
    const binID = process.env.JSON_BIN_ID;

    const accessToken = process.env.JSON_BIN_ACCESS_KEY;

    try {
      const { data } = await axios.get(
        `${process.env.JSON_BIN_URL}/v3/b/${binID}`,
        {
          headers: {
            'X-Access-Key': accessToken,
          },
        }
      );

      const companies = data.record.companies;

      let found = false;

      for (const company of companies) {
        const result = company.phoneNumbers.find(
          (phone: string) => phone === phoneNumber
        );

        if (result) {
          found = true;
          break;
        }
      }

      if (!found) {
        throw new CompanyBlockedException(phoneNumber);
      }

      return data;
    } catch (err) {
      console.error(err);
      throw err;
    }
  }

  static async checkPhoneNumber(phoneNumber: string) {
    console.log('CHECANDO PHONE NUMBER');
    const [error, response] = await ApiService.post(
      'check-phone-number/' + phoneNumber
    );

    console.log('error', error);
    console.log('response', response);

    if (error) {
      if (error?.status !== 403) throw error;

      const data = (error.response?.data as any)?.data;

      console.log('data', data);

      if (data?.freeTierExpired) {
        throw new FreeTierExpired(data?.company?.id);
      } else {
        throw new CompanyBlockedException(data?.company?.id);
      }
    }

    return response;
  }
}

export default AuthService;
