import * as z from 'zod';
import AuthService from '../../services/AuthService.ts';
import { storeInChromeStorage } from '../../utils/chromeStorageHandler.ts';
import CompanyBlockedException from '../../exceptions/auth/CompanyBlockedException.ts';
import FreeTierExpired from '../../exceptions/auth/FreeTierExpired.ts';

class PhoneNumberController {
  static async checkPhoneNumber(payload: any) {
    try {
      const payloadSchema = z.object({
        phoneNumber: z.string(),
      });

      const { phoneNumber } = payloadSchema.parse(payload);

      await AuthService.checkPhoneNumber(phoneNumber);

      storeInChromeStorage('isPhoneNumberAllowed', true);
    } catch (err) {
      const companyBlocked = err instanceof CompanyBlockedException;
      const freeTierExpired = err instanceof FreeTierExpired;

      if (companyBlocked || freeTierExpired) {
        storeInChromeStorage('isPhoneNumberAllowed', false);
        console.log('NÚMERO DE TELEFONE NÃO PERMITIDO');

        storeInChromeStorage('freeTierExpired', freeTierExpired);
      } else {
        storeInChromeStorage('isPhoneNumberAllowed', null);
      }

      throw err;
    }
  }
}

export default PhoneNumberController;
