import { getFromChromeStorage } from '../../utils/chromeStorageHandler.ts';
import getOpenAIInstance from './getOpenAIInstance';

export async function generateAIMessage({ chatInput }) {
  try {
    const { model, systemPrompt } = await getFromChromeStorage('moretoAIModel');

    const openai = await getOpenAIInstance();
    const completion = await openai.chat.completions.create({
      messages: [
        {
          role: 'system',
          content: systemPrompt,
        },
        {
          role: 'user',
          content: chatInput,
        },
      ],
      model: model,
    });

    const iaGeneratedMessage = completion.choices[0].message.content;
    return iaGeneratedMessage;
  } catch (error) {
    throw new Error(error);
  }
}
