import OpenAI from 'openai';
import { getFromChromeStorage } from '../../utils/chromeStorageHandler.ts';

export default async function getOpenAIInstance() {
  try {
    const apiKey = await getFromChromeStorage('tokenOpenAi');
    return new OpenAI({
      apiKey,
      dangerouslyAllowBrowser: true,
    });
  } catch (error) {
    console.error('Erro ao iniciar instância da OpenAi', error);
  }
}
