import { updateBadge } from './background/updateBadge';
import { generateAIMessage } from './background/api/generateAIMessage';
import PhoneNumberController from './background/api/PhoneNumberController.ts';
import ResponseService from './services/ResponseService.ts';

chrome.runtime.onMessage.addListener(function (request, sender, sendResponse) {
  switch (request.action) {
    case 'updateBadge': {
      updateBadge();
      break;
    }
    case 'check-phone-number': {
      PhoneNumberController.checkPhoneNumber(request.payload)
        .then((data) => sendResponse(new ResponseService(true, data)))
        .catch((error) => sendResponse(new ResponseService(false, error)));
      break;
    }
    case 'generateAIMessage': {
      generateAIMessage(request.payload)
        .then((data) => {
          sendResponse({ ok: true, data });
        })
        .catch((error) => sendResponse({ ok: false, error }));
      break;
    }
  }

  return true;
});

chrome.runtime.onInstalled.addListener(async function (details) {
  if (details.reason === 'install' || details.reason === 'update') {
    // updateBadge();
  }
});
