// Salva informação no Chrome Storage da extensão
export async function storeInChromeStorage(key: string, value: any) {
  await chrome.storage.local.set({ [key]: value });
}

// Retorna o value de um key do Chrome Storage da extensão
export function getFromChromeStorage(key:string):Promise<any> {
  return new Promise((resolve, reject) => {
    chrome.storage.local.get(key, function (result) {
      if (chrome.runtime.lastError) {
        reject(chrome.runtime.lastError);
      } else {
        resolve(result[key]);
      }
    });
  });
}
