import EnvVariableNotFoundException from '../exceptions/config/EnvVariableNotFoundException.ts';
import axios, { AxiosError, AxiosInstance, AxiosRequestConfig } from 'axios';

interface ApiService {
  axios: AxiosInstance;
}

type RequestReturn = [AxiosError | undefined, axios.AxiosResponse<any, any> | undefined];

class ApiService {
  constructor() {
    const apiUrl = process.env.API_URL;

    if (!apiUrl) throw new EnvVariableNotFoundException('API_URL');
    this.axios = axios.create({ baseURL: apiUrl });
  }

  async request(
    url: string,
    method: string,
    options: AxiosRequestConfig
  ): Promise<RequestReturn> {
    const optionsWithHeaders: AxiosRequestConfig = {
      ...options,
    };

    try {
      const response = await this.axios({ url, method, ...optionsWithHeaders });
      return [undefined, response];
    } catch (error) {
      return [(error as AxiosError), undefined];
    }
  }

  post(url: string, options: AxiosRequestConfig = {}) {
    return this.request(url, 'post', options);
  }

  get(url: string, options: AxiosRequestConfig = {}) {
    return this.request(url, 'get', options);
  }
}

export default new ApiService();
