interface ResponseService {
  ok: boolean;
  data?: any;
  error?: any;
}

class ResponseService {
  constructor(success: boolean, response: any) {
    this.ok = success;
    if (success) {
      this.data = response;
      this.error = undefined;
    } else {
      this.data = undefined;
      this.error = response;
    }
  }
}

export default ResponseService;
